import React from 'react'

import Header from './Header'
import Landing from './Landing'
import GetTo from './GetTo'


function All() {
    return (
        <div className="all">
            <Header />
            <div className="top">
                <div className="back"></div>
                <Landing/>
                <GetTo />
            </div>
        </div>
    )
}

export default All
